.MainWindow {
	display: block;
	grid-area: main;
}

.TabContainer {
	line-height: 1.5;
	margin-bottom: 2rem;
}

.Tab {
	cursor: pointer;
}

.Tab + .Tab {
	margin-left: 2rem;
}

.Tab:hover {
	text-decoration: underline;
}

.Tab.Selected {
	font-weight: bold;
}



